.section-title {
  font-weight: 500;
  font-size: 32px;
  line-height: 100%;
  text-align: center;
  letter-spacing: -1.5px;

  margin: 0;
  margin-bottom: 48px;

  @include on-tablet {
    font-size: 48px;
  }

  @include on-desktop {
    font-size: 64px;
  }

  &--align-left {
    text-align: left;
  }

  &--contact-us {
    @include on-desktop {
      font-size: 48px;
    }
  }
}

